@import "../colors";
@import "../breakpoints";

.ui-center {
  text-align: center;
}

.carousel-container {
  padding: 100px 0;
  position: relative;
  height: 300px;
  max-width: 960px;
  margin: 0 auto;
  overflow: hidden;
  touch-action: pan-y;
}

.carousel-track {
  display: flex;
  height: 100%;
}

.carousel-card {
  flex: 0 0 300px;
  display: flex;
  justify-content:  flex-start;
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-weight: bold;
  background: white;
  box-shadow: 0 8px 80px #3350AD1F;
  margin: 0 24px 16px;
  border-radius: 16px;
  -webkit-tap-highlight-color: transparent;
  .logo img {
    height: 90%;
    width: 90%;
  }
  .carousel-text {
    line-height: 22px;
    font-size: 14px;
  }
  @media #{$sm} {
    margin: 0 4px 16px;
    padding: 0 20px 0;

  }
  .logo {
    margin-top: 40px;

  }
}

.carousel-card-inner {
  flex-direction: column;
  width: 290px;
  height: 290px;
  border-radius: 5px;
}

.carousel-title {
  margin-top: 20px;
  font-size: 1.5em;
  color: $main-color-dark;
  font-family: "Nunito", Arial, Helvetica, sans-serif;
  text-align: center;
}

.carousel-text {
  padding: 1em;
  color: $text;
  font-size: 16px;
  white-space: pre-wrap;
  font-weight: normal;
  text-align: center;
  font-family: "Nunito", Arial, Helvetica, sans-serif;
}

.carousel-pagination-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 20px;
  display: flex;
  justify-content: center;
  pointer-events: none;
}

.carousel-pagination {
  list-style: none;
  display: flex;
  justify-content: space-around;
  width: 180px;
  padding: 0;
}

.carousel-pagination li {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: rgba(50, 50, 50, 0.5);
}

.carousel-pagination .current {
  border-radius: 0;
  background: white;
}
